<template>
  <div class="wrapper">
    <div class="sliding-text"
         :style=style
         :class="{ paused }"
         @click="paused = !paused">
      {{text}}
    </div>
  </div>
</template>

<script>
  import axios from 'axios';

  export default {
    data() {
      return {
        list: [],
        paused: false,
        speed: '',
        text: '',
        style: ''
      }
    },
    created() {
      axios.get('https://vuejs-http-a3463.firebaseio.com/data.json')
        .then(response => {
          this.speed = response.data.speed;
          this.style = 'animation: text-slide ' + this.speed + 's';
          this.text = response.data.text;
        });
    }
  }
</script>

<style>
  body {
    padding: 0;
    margin: 0;
    font-family: 'Fira Sans Condensed', sans-serif;
  }

  .sliding-text {

    padding: 100px 15px 0;
    position: relative;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
    background-color: #e6e6ee;
    word-break: break-word;
    -webkit-transform: rotate3d(0, 1, 0, 180deg);
    -moz-transform: rotate3d(0, 1, 0, 180deg);
    -ms-transform: rotate3d(0, 1, 0, 180deg);
    -o-transform: rotate3d(0, 1, 0, 180deg);
    transform: rotate3d(0, 1, 0, 180deg);
  }

  .sliding-text.paused {
    -webkit-animation-play-state: paused !important;
    animation-play-state: paused !important;
  }

  .wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
  }

  .overlay {
    background-color: rgba(0, 0, 0, .6);
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
  }

  .overlay.opened {
    display: block;
  }

  body:not(.has-navigation) .overlay.opened .play-btn-big {
    display: block;
  }

  .navigation {
    position: fixed;
    z-index: 10;
    top: 0;
    right: -100%;
    width: 65%;
    height: 100vh;
    background-color: #ebebeb;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    /*transform: rotate3d(0, 1, 0, 180deg);*/
    /*-webkit-transform: rotate3d(0, 1, 0, 180deg);*/
    /*-o-transform: rotate3d(0, 1, 0, 180deg);*/
    padding: 20px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

  .navigation.opened {
    right: 0;
  }

  .navigation-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    height: 100%;
  }

  .play-btn-big {
    display: none;
    position: fixed;
    z-index: 15;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
    width: 100px;
    height: 100px;
    background-image: url('data:image/png;base64,');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    -webkit-animation: playZoom 2s infinite;
    animation: playZoom 2s infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
  }

  .navigation .play-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: start;
    -ms-flex-pack: start;
    justify-content: flex-start;
  }

  .navigation .play-wrap .text {
    font-size: 24px;
    margin-left: 20px;
  }

  .navigation .play-btn {
    width: 60px;
    height: 60px;
    background-image: url('data:image/png;base64,');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    -webkit-animation: playZoom 2s infinite;
    animation: playZoom 2s infinite;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
  }

  .navigation .nav-menu {
    padding: 0;
    margin: 0;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
  }

  .navigation .nav-menu li {
    position: relative;
    padding: 0;
    color: #D29D25;
    font-variant: small-caps;
    font-weight: bold;
    cursor: pointer;
  }

  .nav-menu li:before {
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #4F5151;
    content: "";
    left: 0;
    -webkit-transition: .3s ease-in-out;
    -o-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
    top: 27px;
  }

  .nav-menu li:after {
    position: absolute;
    border-left: 1px dotted #4F5151;
    width: 1px;
    bottom: -12px;
    content: "";
    left: 3px;
    top: 48px;
  }

  .nav-menu li:hover:before {
    -webkit-box-shadow: 0 0 0 10px rgba(0, 0, 0, .2);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, .2)
  }

  .nav-menu li:last-child:after {
    content: none;
  }

  .nav-menu li a {
    font-size: 24px;
    text-decoration: none;
    color: #000000;
    display: block;
    padding: 15px 0 15px 40px;
  }

  @keyframes playZoom {
    0% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
    50% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  @-webkit-keyframes playZoom {
    0% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
    50% {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }

  @-webkit-keyframes text-slide {
    from {
      -webkit-transform: rotate3d(0, 1, 0, 180deg) translateY(0);
      transform: rotate3d(0, 1, 0, 180deg) translateY(0);
    }
    to {
      -webkit-transform: rotate3d(0, 1, 0, 180deg) translateY(-100%);
      transform: rotate3d(0, 1, 0, 180deg) translateY(-100%);
    }
  }

  @keyframes text-slide {
    from {
      -webkit-transform: rotate3d(0, 1, 0, 180deg) translateY(0);
      transform: rotate3d(0, 1, 0, 180deg) translateY(0);
    }
    to {
      -webkit-transform: rotate3d(0, 1, 0, 180deg) translateY(-100%);
      transform: rotate3d(0, 1, 0, 180deg) translateY(-100%);
    }
  }

  .sliding-text {
    /**/
    font-size: 70px;
    /*-webkit-animation: text-slide 450s;*/
    /*animation: text-slide 450s;*/
  }
</style>



